import React from 'react'
import {Panel} from "react-bootstrap";
import Glyphicon from "react-bootstrap/es/Glyphicon";
import ReleaseFeaturesInfoPanel from "./ReleaseFeaturesInfoPanel";
import {FaMoon} from "react-icons/fa";

class NewFeaturesInfoPanel extends React.Component {

    render() {
        return <Panel bsStyle="primary">
            <Panel.Heading>
                <Glyphicon glyph="flash"/> Was ist neu?
            </Panel.Heading>
            <Panel.Body>
                <ReleaseFeaturesInfoPanel version="2.0" date="31.03.2019">
                    <li>
                        <Glyphicon glyph="home"/> berarbeitete Startseite mit bersicht aller Features
                        und Neuerungen
                    </li>
                    <li><Glyphicon glyph="phone"/> Verbessertes Design fr Mobilgerte</li>
                    <li><Glyphicon glyph="stats"/> Neue Statistiken fr Aufnahmen</li>
                    <li><Glyphicon glyph="filter"/> Statistiken knnen jetzt gefilter werden</li>
                    <li><Glyphicon glyph="screenshot"/> Finish-Anzeige whrend des Spiels</li>
                    <li><FaMoon/> Darktheme verfgbar (klicke auf das Symbol in der linken oberen Ecke
                        zum Wechseln des Designs)
                    </li>
                    <li>
                        <Glyphicon glyph="eject"/> Mglichkeit zum Ausblenden der Navigationsleiste whrend eines Spiels
                    </li>
                </ReleaseFeaturesInfoPanel>
            </Panel.Body>
        </Panel>
    }
}

NewFeaturesInfoPanel.propTypes = {};

export default NewFeaturesInfoPanel